import React, {useState} from 'react'

const Nav = () => {

 const [burgerState, setburgerState] = useState(false)

    const func = () => {
           setburgerState(!burgerState)
    }
        const classState = () => {
            let classes = 'navLinks'

            if(burgerState) {
                classes += ' active'
            }
            return classes
        }

    return (
        <nav>
            <div className='logo'>
                <i className="fab fa-apple"></i>
                <h4> Tindow </h4>
            </div>
            <ul className = {classState()} >

            <li className = 'navLink'><a href='/'>Home</a></li>
            <li className = 'navLink'><a href='/'>Buy Now</a></li>
            <li className = 'navLink'><a href='/'>How it works</a></li>
            <li className = 'navLink'><a href='/'>Contact us</a></li>
           <li className = 'navLink'><a href='/'><i class="fas fa-shopping-cart"></i></a></li>

            </ul>
            <div className='hamburger-toggle'>
            <i className= 'fas fa-bars fa-lg'  onClick={func} />
            </div>
        </nav>
    )
}

export default Nav
